<template>
  <div class="content-header">
    <h1 class="title">
      
    </h1>
  </div>
  <div class="dashboard-content-container">
    <div class="dashboard-content-group">
      <div class="dashboard-content">
        <div id="totalDayoffGage"/>
      </div>
      <div class="dashboard-content">
        <CommonInfoCard :title="'   '"
                       :content="`: ${dashBoardInfo.recentUseDayoffName}\n: ${dashBoardInfo.recentUseDayoffDate}\n: ${dashBoardInfo.recentUseDayoffCreateDt}`"/>
      </div>
    </div>
    <div class="dashboard-content-group">
      <div class="dashboard-content">
        <CommonInfoCard :title="'   '"
                        :content="`: ${dashBoardInfo.mostRemainingDayoffName}\n  : ${dashBoardInfo.mostRemainingDayoffCount}`"></CommonInfoCard>
      </div>
      <div class="dashboard-content">
        <CommonInfoCard :title="'   '"
                        :content="`: ${dashBoardInfo.muchHolidayWorkerName}\n  : ${dashBoardInfo.muchHolidayWorkerWorkDateCount}`"></CommonInfoCard>
      </div>
    </div>
  </div>

</template>

<script>
import bb, {gauge} from "billboard.js"
import DashBoardProtocol from "@/network/dashBoardProtocol";
import CommonInfoBox from "@/views/dashBoard/CommonInfoBox.vue";
import CommonInfoCard from "@/views/dashBoard/CommonCard.vue";
export default {
  name: 'DashBoardView',
  components: {CommonInfoCard, CommonInfoBox},
  data () {
    return {
      year: new Date().getFullYear(),
      dashBoardInfo: new DashBoardProtocol.DashBoardInfo({}),
      contentHeight: this.contentHeight,
      contentWidth: this.contentWidth
    }
  },
  methods: {
    async getDashBoardInfo(){
      this.dashBoardInfo = await DashBoardProtocol.getDashBoardInfo({year:this.year});

      const totalDayoffCountGauge = this.createTotalDayoffCountGauge();
      totalDayoffCountGauge.load({});
    },
    createTotalDayoffCountGauge() {
      return bb.generate({
        data: {
          columns: [
            ["  ", this.dashBoardInfo.totalUseDayoffCount],
            ["  ", this.dashBoardInfo.totalRemainingDayoffCount],
          ],
          type:gauge(),
        },
        gauge: {
          label:{
            format(value) {
              return value;
            }
          },
          title:"   ",
          max:this.dashBoardInfo.totalRemainingDayoffCount + this.dashBoardInfo.totalUseDayoffCount,
          min:0
        },
        size: {
          width: 260,
          height: 180
        },
        bindto: "#totalDayoffGage"
      });
    },
  },
  mounted() {
    this.getDashBoardInfo();
  },
};
</script>

<style scoped>
@import "@/assets/css/task.css";
</style>